<script>
import '@/scss/common.css'
export default {
  name: 'Announce',
  data () {
    return {
      showMoreRoute: false,
      showMobileRoute: false,
      mouseOverRoutes: false,
      pdfUrl: ''
    }
  },
  mounted () {
    var clientWidth = document.documentElement.clientWidth
    var screenWidth = window.screen.width
    var innerWidth = window.innerWidth
    console.info('clientWidth:', clientWidth, ' screenWidth:', screenWidth, 'innerWidth:', innerWidth)
    var url = this.getAssetsBasePageUrl()
    this.pdfUrl = url + 'static/Atlas.pdf?v=' + process.env.VUE_APP_VER
    console.log('pdfUrl:', this.pdfUrl)
    // var link = document.querySelector("link[rel*='icon']") || document.createElement('link')
    // link.type = 'image/x-icon'
    // link.rel = 'shortcut icon'
    // link.href = url + 'static/favicon.ico'
    // document.getElementsByTagName('head')[0].appendChild(link)
  },
  methods: {
    onTapBG: function (e) {
      console.log('onTapBG', e)
      this.showMoreRoute = false
      this.showMobileRoute = false
      this.mouseOverRoutes = false
    },
    onTapNav: function (section) {
      console.log('onTapNav1:', section)
      if (section) {
        console.log('onTapNav111')
        this.$router.push({
          name: 'Home',
          params: {
            section
          }
        })
      } else {
        console.log('onTapNav222')
        this.$router.push({
          name: 'Home'
        })
      }
    },
    onTapMobileRoute: function (e) {
      console.log('onTapMobileRoute', e)
      this.showMobileRoute = !this.showMobileRoute
    },
    onShowMoreRoute: function () {
      this.showMoreRoute = true
    },
    onHideMoreRoute: function () {
      setTimeout(() => {
        this.resetShowMoreRoute()
      }, 400)
    },
    resetShowMoreRoute: function () {
      if (!this.mouseOverRoutes) {
        this.showMoreRoute = false
      }
    },
    jumpBlog: function () {
      window.open('https://medium.com/atlastoken')
    },
    jumpAirdrop: function () {
      window.open('https://docs.google.com/forms/d/1BLsx03HpHsQSv-wQZ23-wfMUzd1hfjY0s92wFFlRJO4/viewform?edit_requested=true')
    },
    onMouseEnterRoutes: function () {
      console.log('onMouseEnterRoutes')
      this.mouseOverRoutes = true
    },
    onMouseLeaveRoutes: function () {
      console.log('onMouseLeaveRoutes')
      this.mouseOverRoutes = false
      this.showMoreRoute = false
    },
    onTapMoreRoute: function (e) {
      console.log('onTapMoreRoute', e)
      this.showMoreRoute = true
      // this.showMoreRoute = !this.showMoreRoute
    },
    onTapPDF: function () {
      window.open(this.pdfUrl)
    },
    getAssetsBasePageUrl: function () {
      var loc = location.href
      var base = loc.split('#')[0]
      return base
    }
  }
}
</script>

